import { AppBar, IconButton, Typography, TextField, InputAdornment } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';



function NavBar() {
    return (
        <AppBar position='static'>
            <div className='bg-gray-900 flex flex-col sm:flex-row  p-2 sm:items-center'>
                <div className='flex items-center'>
                <IconButton color='inherit'><MenuIcon /></IconButton>
                    <Typography variant='h6'>API Weather App</Typography>
                </div>
                <div className='bg-red-90  justify-center flex'>
                    <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
                        InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <SearchIcon />
                              </InputAdornment>
                            ),
                          }}
                        className='w-11/12 bg-white text-black rounded-xl'/>
                </div>

            </div>
        </AppBar>
    )
};


export default NavBar;